import React from "react";
import clsx from "clsx";

interface BrandLogoProps {
  className?: string;
  variant?: "white" | "orange" | "black";
}

export const BrandLogo: React.FC<BrandLogoProps> = ({
  className,
  variant = "orange",
}) => {
  return (
    <img
      className={clsx(className)}
      src="/static/media/svg/salt-logo.svg"
      alt="Session"
    />
  );
};
